import {CREATE_PRODUCT_NAME, DELETE_PRODUCT_NAME, FAIL_PRODUCT_NAME, GET_PRODUCT_NAME, UPDATE_PRODUCT_NAME, UPDATE_PRODUCT_NAME_FAIL} from '../../typeXarid'
import axios from '../../../axios'
export const getProoductData = () => async (dispatch) => {
    try {
        const { data } = await axios.get('http://192.168.60.58:8000/api/finance/productname/')
        dispatch({
            type: GET_PRODUCT_NAME,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: FAIL_PRODUCT_NAME,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const productData = (manba) => async (dispatch) => {
    try {
        const { data } = await axios.post('finance/origin/',{
              name:manba
        })

        dispatch({
            type: CREATE_PRODUCT_NAME,
            payload: data
        })
     
 
    } catch (error) {
        dispatch({
            type: FAIL_PRODUCT_NAME,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const updateProduct = (manba,id) => async (dispatch, getState) => {
    try {
        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }
        console.log(config)

        const { data } = await axios.put( `finance/origin/${id}/`, {
			name: manba
		});

        
        dispatch({
            type: UPDATE_PRODUCT_NAME,
            payload: data,
            id:id
        })


        


    } catch (error) {
        dispatch({
            type: UPDATE_PRODUCT_NAME_FAIL,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const deleteProduct = (id) => async (dispatch, getState) => {
    try {
        const { data } = await axios.delete(
            `finance/origin/${id}/`,
       
        )
        dispatch({
            type: DELETE_PRODUCT_NAME,
            id:id,
            payload:data
        })

        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        console.log(config)
     

    

    } catch (error) {
       console.log(error)
    }
}

