import {
  CREATE_KONT,
  DELETE_KONT,
  GET_KONT,
  UPDATE_KONT
    
  } from '../../typeKassa'
  const initialState = {
    kont:[]
  }
  

  
  export const kontReducer = (state=initialState, action) => {
      switch (action.type) {
          case GET_KONT:
            return{
              ...state,
              kont:action.payload
            }

          case CREATE_KONT:
            const arr = []
            arr.push(action.payload)
              return { 
                  ...state,
                  kont:[...state.kont,...arr]
                    }
          case UPDATE_KONT:
                      return {
                        ...state,
                        kont: state.kont.map((contact) =>
                          contact.id == action.id ? action.payload : contact
                        ),
                      };
          case DELETE_KONT:
            const id = action.id    
             return {
          ...state,
          kont: state.kont.filter((card) => card.id !== id)
            }
            
  
          default:
              return state
      }
  }
  
  
  
