import axios from 'axios'
import {CREATE_KONT,DELETE_KONT, PRODUCT_DELETE_FAIL,FAIL_KONT,GET_KONT,UPDATE_KONT,UPDATE_FAIL} from '../../typeKassa'


export const getKontData = () => async (dispatch) => {
    try {
        
        const { data } = await axios.get('http://blmoliya.uz/api/finance/counter/')
        dispatch({
            type: GET_KONT,
            payload: data
        })
      
 
    } catch (error) {
        dispatch({
            type: FAIL_KONT,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}

export const kontData = (cont) => async (dispatch) => {
    try {
        
        const { data } = await axios.post('http://blmoliya.uz/api/finance/counter/',{
              name:cont
        })

        dispatch({
            type: CREATE_KONT,
            payload: data
        })
    
 
    } catch (error) {
        dispatch({
            type: FAIL_KONT,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const updateKont = (kont,id) => async (dispatch, getState) => {
    try {
        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const { data } = await axios.put(`http://blmoliya.uz/api/finance/counter/${id}/`, {
			name: kont
		});

        
        dispatch({
            type: UPDATE_KONT,
            payload: data,
            id:id
        })


        


    } catch (error) {
        dispatch({
            type: UPDATE_FAIL,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const deleteKont = (id) => async (dispatch, getState) => {
    try {
        const { data } = await axios.delete(
            `http://blmoliya.uz/api/finance/counter/${id}/`,
       
        )
        dispatch({
            type: DELETE_KONT,
            id:id,
            payload:data
        })

        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

     

    

    } catch (error) {
        dispatch({
            type: PRODUCT_DELETE_FAIL,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}

