import {POST_XARID_LIST,FAIL_XARID} from '../../typeXarid'
import axios from '../../../axios'

export const postXaridData = (e) => async (dispatch) => {
      try {
          
          const {data} = await axios.post('http://192.168.60.58:8000/api/sales/sotuv/',e)
          dispatch({
              type: POST_XARID_LIST,
              payload: data
          })
        
   
      } catch (error) {
          dispatch({
              type: FAIL_XARID,
              payload: error.response && error.response.data.detail
                  ? error.response.data.detail
                  : error.message,
          })
      }
  }