import {
     CREATE_MANBA,DELETE_MANBA,GET_MANBA, UPDATE_MANBA,
        
      } from '../../typeKassa'
      const initialState = {
        manba:[]
      }
      
      export const manbaReducer = (state=initialState, action) => {
          switch (action.type) {
              case GET_MANBA:
                return{
                  ...state,
                  manba:action.payload
                }
    
              case CREATE_MANBA:
                const arr = []
                arr.push(action.payload)
                  return { 
                      ...state,
                      manba:[...state.manba,...arr]
                        }
              case UPDATE_MANBA:
                          return {
                            ...state,
                            manba: state.manba.map((contact) =>
                              contact.id == action.id ? action.payload : contact
                            ),
                          };
              case DELETE_MANBA:
                const id = action.id    
                 return {
              ...state,
            manba: state.manba.filter((card) => card.id !== id)
                }
                
      
              default:
                  return state
          }
      }
      
      
      

      