import {FAIL_XARID_TABLE, GET_XARID_TABLE,DELETE_XARID_TABLE} from '../../typeXarid'
import axios from 'axios'

export const postXaridData = (e) => async (dispatch) => {
      try {
          const {data} = await axios.get('http://192.168.60.58:8000/api/sales/sotuv/')
          dispatch({
              type: GET_XARID_TABLE,
              payload: data
          }) 
      } catch (error) {
          dispatch({
              type: FAIL_XARID_TABLE,
              payload: error.response && error.response.data.detail
                  ? error.response.data.detail
                  : error.message,
          })
      }
  }

  export const deleteXaridTable =(id)=>async dispatch =>{
    try {
      dispatch({
        type: DELETE_XARID_TABLE,
        payload: id,
      })
  
   
      const {data}= await axios.delete(`http://blmoliya.uz/api/finance/table/${id}`)  
  
    } catch(error){
      console.log(error)
    }
  }