import React, { useState } from 'react';
import { Button, Card, CardBody, Col, FormGroup, Form, Input, InputGroup, Row ,Table} from 'reactstrap';
import { useDispatch } from 'react-redux';
import { kontData } from '../../redux/action/kassa/kontAction';
function InputFirstData() {
	const [name, setName] = useState('');
	const dispatch = useDispatch();
	const postName = async () => {
		dispatch(kontData(name));
		setName('');
	};
	// const lastItem = data[data.length - 1];
	return (
		<>
			<Row className="mt-4">
				<Col lg="12" md="12" style={{ marginBottom: 10 }}>
					<Card className="shadow border-0" style={{ backgroundColor: '#E5F0FF' }}>
						<CardBody className="px-lg-5 py-lg-3">
							<div className="text-center text-muted mb-4">
								<small
									style={{
										fontSize: '16px',
										color: 'gray',
										fontWeight: '500',
										textAlign: 'center',
										marginTop: '10px'
									}}
								>
									  
								</small>
							</div>

							<Form role="form">
								<Row>
									<Col lg={4}>
										<FormGroup>
											<InputGroup className="input-group-alternative mb-3">
												<Input
													placeholder=""
													type="text"
													autoComplete="new-hisob"
													value={name}
													onChange={(e) => setName(e.target.value)}
												/>
												<Button
													onClick={postName}
													style={{ background: '#5E72E4', border: 'none' }}
												>
													<i class="fas fa-plus"></i>
												</Button>
											</InputGroup>
										</FormGroup>
									</Col>
									
								</Row>
							</Form>
							<Card>
							<Table className="align-items-center table-flush" responsive>
							<thead className="thead-light">
								<tr>
									<th scope="col"> </th>
									<th scope="col"></th>
								</tr>
							</thead>
							<tbody>
								<tr>
									<td>test</td>
									<td>test</td>
								</tr>
					
							</tbody>
						</Table>
							</Card>
							
						</CardBody>
					</Card>
				</Col>
			</Row>
		</>
	);
}

export default InputFirstData;
