
import {CREATE_EMPLOYEE,DELETE_EMPLOYEE,UPDATE_EMPLOYEE,FAIL_EMPLOYEE,GET_EMPLOYEE, UPDATE_EMPLOYEE_FAIL} from '../../typeKassa'
import axios from '../../../axios'


export const getEmployeeData = () => async (dispatch) => {
    try {
        
        const { data } = await axios.get('finance/employes/')
        dispatch({
            type: GET_EMPLOYEE,
            payload: data
        })
      
 
    } catch (error) {
        dispatch({
            type: FAIL_EMPLOYEE,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}

export const employeeData = (manba) => async (dispatch) => {
    try {
        
        const { data } = await axios.post('finance/employes/',{
              name:manba
        })

        dispatch({
            type: CREATE_EMPLOYEE,
            payload: data
        })
     
 
    } catch (error) {
        dispatch({
            type: FAIL_EMPLOYEE,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const updateEmployee = (manba,id) => async (dispatch, getState) => {
    try {
        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const { data } = await axios.put( `finance/employes/${id}/`, {
			name: manba
		});

        
        dispatch({
            type: UPDATE_EMPLOYEE,
            payload: data,
            id:id
        })


        


    } catch (error) {
        dispatch({
            type: UPDATE_EMPLOYEE_FAIL,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const deleteEmployee = (id) => async (dispatch, getState) => {
    try {
        const { data } = await axios.delete(
            `finance/employes/${id}/`,
       
        )
        dispatch({
            type: DELETE_EMPLOYEE,
            id:id,
            payload:data
        })

        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

     

    

    } catch (error) {
       console.log(error)
    }
}

