
import {CREATE_MANBA,DELETE_MANBA,UPDATE_MANBA,FAIL_MANBA,GET_MANBA, UPDATE_MANBA_FAIL} from '../../typeKassa'
import axios from '../../../axios'


export const getManbaData = () => async (dispatch) => {
    try {
        
        const { data } = await axios.get('finance/origin/')
        dispatch({
            type: GET_MANBA,
            payload: data
        })
      
 
    } catch (error) {
        dispatch({
            type: FAIL_MANBA,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}

export const manbaData = (manba) => async (dispatch) => {
    try {
        
        const { data } = await axios.post('finance/origin/',{
              name:manba
        })

        dispatch({
            type: CREATE_MANBA,
            payload: data
        })
     
 
    } catch (error) {
        dispatch({
            type: FAIL_MANBA,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const updateManba = (manba,id) => async (dispatch, getState) => {
    try {
        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const { data } = await axios.put( `finance/origin/${id}/`, {
			name: manba
		});

        
        dispatch({
            type: UPDATE_MANBA,
            payload: data,
            id:id
        })


        


    } catch (error) {
        dispatch({
            type: UPDATE_MANBA_FAIL,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}
export const deleteManba = (id) => async (dispatch, getState) => {
    try {
        const { data } = await axios.delete(
            `finance/origin/${id}/`,
       
        )
        dispatch({
            type: DELETE_MANBA,
            id:id,
            payload:data
        })

        const {
            userLogin: { userInfo },
        } = getState()

        const config = {
            headers: {
                'Content-type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

     

    

    } catch (error) {
       console.log(error)
    }
}

