import React, { useState, useEffect } from 'react';
import { Button, Card, CardBody, Col, FormGroup, Form, Input, InputGroup, Row } from 'reactstrap';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { kontData } from '../../redux/action/kassa/kontAction';
import { manbaData } from 'redux/action/kassa/manbaAction';
import { employeeData } from 'redux/action/kassa/employeeAction';
function InputFirstData() {
	const [kont, setKont] = useState('');
	const [manba, setManba] = useState('');
	const [employee, setEmployee] = useState('');
	const dispatch = useDispatch();
	const postKont = async () => {
		dispatch(kontData(kont));
		setKont('');
	};
	const postManba = async (e) => {
		e.preventDefault();
		dispatch(manbaData(manba));
		setManba('');
	};
	const postEmployee = async (e) => {
		e.preventDefault();
		dispatch(employeeData(employee));
		setEmployee('');
	};

	// const lastItem = data[data.length - 1];
	return (
		<>
			<Row className="mt-4">
				<Col lg="12" md="12" style={{ marginBottom: 10 }}>
					<Card className="shadow border-0" style={{ backgroundColor: '#E5F0FF' }}>
						<CardBody className="px-lg-5 py-lg-3">
							<div className="text-center text-muted mb-4">
								<small
									style={{
										fontSize: '16px',
										color: 'gray',
										fontWeight: '500',
										textAlign: 'center',
										marginTop: '10px'
									}}
								>
									 
								</small>
							</div>

							<Form role="form">
								<Row>
									<Col lg={4}>
										<FormGroup>
											<InputGroup className="input-group-alternative mb-3">
												<Input
													placeholder=""
													type="text"
													autoComplete="new-hisob"
													value={kont}
													onChange={(e) => setKont(e.target.value)}
												/>
												<Button
													onClick={postKont}
													style={{ background: '#5E72E4', border: 'none' }}
												>
													<i class="fas fa-plus"></i>
												</Button>
											</InputGroup>
										</FormGroup>
									</Col>
									<Col lg={4}>
										<FormGroup>
											<InputGroup className="input-group-alternative">
												<Input
													placeholder=""
													type="text"
													autoComplete="manba"
													value={manba}
													onChange={(e) => setManba(e.target.value)}
												/>
												<Button
													onClick={postManba}
													style={{ background: '#5E72E4', border: 'none' }}
												>
													<i class="fas fa-plus"></i>
												</Button>
											</InputGroup>
										</FormGroup>
									</Col>
									<Col lg={4}>
										<FormGroup>
											<InputGroup className="input-group-alternative mb-3">
												<Input
													placeholder=""
													type="text"
													autoComplete="new-hisob"
													value={employee}
													onChange={(e) => setEmployee(e.target.value)}
												/>
												<Button
													onClick={postEmployee}
													style={{ background: '#5E72E4', border: 'none' }}
												>
													<i class="fas fa-plus"></i>
												</Button>
											</InputGroup>
										</FormGroup>
									</Col>
								</Row>
							</Form>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</>
	);
}

export default InputFirstData;
