import React from 'react';

// reactstrap components
import { Card, CardBody, Container, Row, Col } from 'reactstrap';
import ModalEqual from './ModalsEqual';
import ModalKirim from './ModalKirim';
import ModalOutlays from './ModalOutlays';
import ModalSalary from './ModalSalary';

const Header = () => {
	return (
		<>
			{/* <div className="header bg-gradient-info pb-8 pt-5 pt-md-8"> */}
			<Container fluid className="bg-gradient-info" style={{ height: '47.6vh' }}>
				{/* <div className="header-body"> */}
				{/* Card stats */}
				<Row>
					<Col lg="6" xl="3">
						<Card className="card-stats mb-4 mb-xl-0" style={{ background: 'transparent' }}>
							<ModalKirim />
						</Card>
					</Col>
					<Col lg="6" xl="3">
						<Card className="card-stats mb-4 mb-xl-0" style={{ background: 'transparent' }}>
							<ModalOutlays />
						</Card>
					</Col>
					<Col lg="6" xl="3">
						<Card className="card-stats mb-4 mb-xl-0" style={{ background: 'transparent' }}>
							<ModalSalary />
						</Card>
					</Col>
					<Col lg="6" xl="3">
						<Card className="card-stats mb-4 mb-xl-0" style={{ background: 'transparent' }}>
							<ModalEqual />
						</Card>
					</Col>
				</Row>
				{/* </div> */}
			</Container>
			{/* </div> */}
		</>
	);
};

export default Header;
