import {
      CREATE_ROW,
      GET_ROW,
    
      DELETE_ROW,
      SELECT_ROW,
      UPDATE_ROW,
    } from '../../types';
    import axios  from 'axios'
    
export const getKassaData = (e) => async (dispatch) => {
          try {
              const {data} = await axios.get('http://blmoliya.uz/api/finance/table/')
              dispatch({
                  type: GET_ROW,
                  payload: data
              }) 
          } catch (error) {
            console.log(error)
          }
      }
export const updateTable = (row,id) => async (dispatch, getState) => {
        try {
           
    
            const { data } = await axios.put(`http://blmoliya.uz/api/finance/table/${id}/`, {
          name: row
        });
    
            
            dispatch({
                type: UPDATE_ROW,
                payload: data,
                id:id
            })
    
    
            
    
    
        } catch (error) {
          console.log(error)
        }
    }
    
  
    export const deleteContact =(id)=>async dispatch =>{
      try {
        dispatch({
          type: DELETE_ROW,
          payload: id,
        })
    
     
        const {data}= await axios.delete(`http://blmoliya.uz/api/finance/table/${id}`)  
    
      } catch(error){
        console.log(error)
      }
    }
    // select all contact
 
    
 
    
    export const addContact = contact => async (dispatch) => {
      try {
        const { data } = await axios.post(
          `http://blmoliya.uz/api/finance/table/`,
         contact,
      )  
          dispatch({
            type: CREATE_ROW,
            payload: data,
          })
          
    
      } catch (error) {
       
          console.log(error);
      }
    }