import React from 'react';
// reactstrap components
import { Alert } from 'reactstrap';

class Alerts extends React.Component {
	render() {
		return (
			<>
				<Alert color="warning">
					<span className="alert-inner--text">
						<strong>{this.props.message}</strong>
					</span>
				</Alert>
			</>
		);
	}
}

export default Alerts;
