import { UPDATE_ROW } from "redux/types";
import { CREATE_ROW } from "redux/types";
import { DELETE_ROW } from "redux/types";
import { GET_ROW} from "redux/types";

    
    const intialState = {
      contacts: [
      ],
    };
    
    export const contactReducer = (state = intialState, action) => {
      switch (action.type) {
        case CREATE_ROW:
          const arr = []
            arr.push(action.payload)
          return {
            ...state,
            contacts:[...state.contacts,...action.payload]
          }
        case GET_ROW:
          return {
            ...state,
            contacts: action.payload,
          };
          case UPDATE_ROW:
                      return {
                        ...state,
                        contacts: state.kont.map((contact) =>
                          contact.id == action.id ? action.payload : contact
                        ),
                      };
       
        case DELETE_ROW:
          return {
            ...state,
            contacts: state.contacts.filter(
              (contact) => contact.id !== action.payload
            ),
          };  
        default:
          return state;
      }
    };
    