import axios from 'axios'
import {GET_FURA,FAIL_FURA} from 'redux/typeXarid'
export const getFuraData = () => async (dispatch) => {
    try {
        const { data } = await axios.get('http://192.168.60.58:8000/api/sales/fura/')
        dispatch({
            type: GET_FURA,
            payload: data
        })
    } catch (error) {
        dispatch({
            type: FAIL_FURA,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}