import React, { useState } from 'react';
import {
	Button,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	Input,
	Form,
	InputGroup,
	InputGroupAddon,
	InputGroupText,
	FormGroup
} from 'reactstrap';
import { BsPencil } from 'react-icons/bs';
import { useDispatch } from 'react-redux';
import { updateManba} from '../../../../redux/action/kassa/manbaAction';
import axios from 'axios';

const ModalExample = (props) => {
	const { buttonLabel, className, id } = props;
	const [names, setName] = useState('');

	const dispatch = useDispatch();
	const putManba = async (e) => {
		e.preventDefault();
		dispatch(updateManba(names, id));
		setName('');
		setModal(!modal);
	};

	const [modal, setModal] = useState(false);

	const toggle = () => setModal(!modal);

	return (
		<div>
			<BsPencil
				color="primary"
				onClick={toggle}
				style={{ fontSize: '24px', color: 'blue', cursor: 'pointer', marginRight: '30' }}
			/>
			<Modal isOpen={modal} toggle={toggle} className={className}>
				<ModalHeader toggle={toggle}>{''}</ModalHeader>
				<ModalBody>
					<Form onSubmit={putManba} role="button" method="PUT">
						<FormGroup className="mb-3">
							<InputGroup className="input-group-alternative">
								<InputGroupAddon addonType="prepend">
									<InputGroupText style={{ background: '#F0EAD6' }}>
										<i class="fas fa-keyboard"></i>
									</InputGroupText>
								</InputGroupAddon>
								<Input
									placeholder=""
									type="text"
									autoComplete="new-name"
									style={{ background: '#F0EAD6' }}
									value={names}
									onChange={(e) => setName(e.target.value)}
								/>
							</InputGroup>
						</FormGroup>
						<Button color="primary" type="submit">
							Ok
						</Button>{' '}
					</Form>
				</ModalBody>
				<ModalFooter></ModalFooter>
			</Modal>
		</div>
	);
};

export default ModalExample;
