import {
      GET_XARID_TABLE,
      DELETE_XARID_TABLE,
      
    } from "../../typeXarid"    
    const intialState = {
     table: [],
    };
    
    export const tableReducer = (state = intialState, action) => {
      switch (action.type) {
        case GET_XARID_TABLE:
          return {
            ...state,
            table: action.payload,
          };
        case DELETE_XARID_TABLE:
          return {
            ...state,
            table: state.table.filter(
              (contact) => contact.id !== action.payload
            ),
          };
        default:
          return state;
      }
    };
    