import axios from 'axios'
import {GET_ACCOUNT,FAIL_ACCOUNT} from '../../typeKassa'
export const getAcData = () => async (dispatch) => {
    try {
        
        const { data } = await axios.get('http://blmoliya.uz/api/finance/accountnumber/')
        dispatch({
            type: GET_ACCOUNT,
            payload: data
        })
      
 
    } catch (error) {
        dispatch({
            type: FAIL_ACCOUNT,
            payload: error.response && error.response.data.detail
                ? error.response.data.detail
                : error.message,
        })
    }
}







