import {
     CREATE_EMPLOYEE,
     DELETE_EMPLOYEE,
     GET_EMPLOYEE,
     UPDATE_EMPLOYEE
        
      } from '../../typeKassa'
      const initialState = {
        employee:[]
      }
      
    
      
      export const employeeReducer = (state=initialState, action) => {
          switch (action.type) {
              case GET_EMPLOYEE:
                return{
                  ...state,
                  employee:action.payload
                }
    
              case CREATE_EMPLOYEE:
                const arr = []
                arr.push(action.payload)
                  return { 
                      ...state,
                      employee:[...state.employee,...arr]
                        }
              case UPDATE_EMPLOYEE:
                          return {
                            ...state,
                            employee: state.employee.map((contact) =>
                              contact.id == action.id ? action.payload : contact
                            ),
                          };
              case DELETE_EMPLOYEE:
                const id = action.id    
                 return {
              ...state,
              employee: state.employee.filter((card) => card.id !== id)
                }
                
      
              default:
                  return state
          }
      }
      
      
       
      