import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Row } from 'reactstrap';
import { FcPlus } from 'react-icons/fc';
import { useEffect } from 'react';
import { Card, CardBody, Col, FormGroup, Form, Input, InputGroup } from 'reactstrap';
import axios from 'axios';

const ModalExample = ({ buttonLabel, className, title }) => {
	const [modal, setModal] = useState(false);
	const [kont, setKont] = useState('');
	const [hisob, setHisob] = useState('');
	const [hodim, setHodim] = useState('');
	const [manba, setManba] = useState('');

	const postHisobot = (e) => {
		e.preventDefault();

		const dataInput = {
			cashmoneys: kont,
			plasticmoneys: manba,
			accountnumbermoneys: hodim,
			dollars: hisob
		};
		axios.post('http://blmoliya.uz/api/finance/endvalue/', dataInput);
		setKont('');
		setHisob('');
		setHodim('');
		setManba('');
	};

	const toggle = () => setModal(!modal);
	useEffect(() => {
		async function asyncFunc() {
			// fetch data from a url endpoint
			const { data } = await axios.post('');
		}
	}, [kont, modal]);

	return (
		<div>
			<Button color="danger" onClick={toggle} style={{ width: '100%' }} disabled>
				  
			</Button>
			<Modal isOpen={modal} toggle={toggle} className={className}>
				<ModalHeader toggle={toggle}>
					<div className="text-center text-muted mb-4">
						<small
							style={{
								fontSize: '16px',
								color: 'gray',
								fontWeight: '500',
								textAlign: 'center',
								marginTop: '10px'
							}}
						>
							  
						</small>
					</div>
				</ModalHeader>

				<ModalBody>
					<Row>
						<Col lg="12" md="12" style={{ marginBottom: 10 }}>
							<Card className="shadow border-0" style={{ backgroundColor: '#E5F0FF' }}>
								<CardBody className="px-lg-5 py-lg-3">
									<Form role="form">
										<FormGroup>
											<InputGroup className="input-group-alternative mb-3">
												<Input
													placeholder=""
													type="number"
													value={kont}
													onChange={(e) => setKont(e.target.value)}
												/>
											</InputGroup>
										</FormGroup>
										<FormGroup>
											<InputGroup className="input-group-alternative mb-3">
												<Input
													placeholder=""
													type="number"
													autoComplete="new-hisob"
													value={hisob}
													onChange={(e) => setHisob(e.target.value)}
												/>
											</InputGroup>
										</FormGroup>
										<FormGroup>
											<InputGroup className="input-group-alternative">
												<Input
													placeholder=""
													type="number"
													autoComplete="manba"
													value={manba}
													onChange={(e) => setManba(e.target.value)}
												/>
											</InputGroup>
										</FormGroup>
										<FormGroup>
											<InputGroup className="input-group-alternative">
												<Input
													placeholder="X/"
													type="number"
													autoComplete="new-hodim"
													value={hodim}
													onChange={(e) => setHodim(e.target.value)}
												/>
											</InputGroup>
										</FormGroup>

										<div className="text-center">
											<Button
												className="mt-4"
												color="primary"
												type="button"
												onClick={postHisobot}
											>
												
											</Button>
										</div>
									</Form>
								</CardBody>
							</Card>
						</Col>
					</Row>
				</ModalBody>
				{/* <ModalFooter>
					<FcPlus style={{ fontSize: 50, cursor: 'pointer' }} />
				</ModalFooter> */}
			</Modal>
		</div>
	);
};

export default ModalExample;
