import React from 'react';
import Header from 'app/pages/Purchues/components/Headerp';
// reactstrap components
import { Container, Row, Col } from 'reactstrap';
import XaridInput from 'components/Xarid/InputXarid';
import PieChart from 'charts/Pie';
import Bar from 'charts/Vertical';
import XaridTable from 'app/pages/Purchues/components/XaridTable';

const Index = (props) => {
	return (
		<>
			<Header />
			<Container className="mt--7" fluid>
				<Row>
					<Col className="mb-5 mb-xl-0" xl="6">
						<Bar />
					</Col>
					<Col className="mb-5 mb-xl-0" xl="6">
						<PieChart />
					</Col>
				</Row>
				<XaridInput />
				<XaridTable />
			</Container>
		</>
	);
};

export default Index;
