import React, { useState, useEffect } from 'react';
import { Button, Modal, ModalHeader, ModalBody, Row } from 'reactstrap';

import { Card, CardBody, Col, FormGroup, Form, Input, InputGroup } from 'reactstrap';
import axios from 'axios';
import Select from 'react-select';
const createOption = (label, value) => ({
	label,
	value
});
const ModalExample = ({ buttonLabel, className, title }) => {
	const [kont, setKont] = useState('');
	const [modal, setModal] = useState(false);
	const [izoh, setIzoh] = useState('');
	const [dataAccount, setDataAccount] = useState([]);
	const [handDate, setHandDate] = useState('');
	const [dataEmploye, setDataEmploye] = useState([]);
	const [chiqim, setChiqim] = useState('');
	const [dataMonth, setDataMonth] = useState([]);
	useEffect(() => {
		axios
			.get('http://blmoliya.uz/api/finance/employes/')
			.then((resp) => {
				setDataEmploye(resp.data);
				console.log(resp.data);
			})
			.catch((err) => {
				// Handle Error Here
				console.error(err);
			});
		axios
			.get('http://blmoliya.uz/api/finance/accountnumber/')
			.then((resp) => {
				setDataAccount(resp.data);
			})
			.catch((err) => {
				// Handle Error Here
			});

		axios
			.get('http://blmoliya.uz/api/finance/month/')
			.then((resp) => {
				setDataMonth(resp.data);
			})
			.catch((err) => {
				// Handle Error Here
			});
	}, []);

	const defaultOption2 = dataEmploye.map((e) => createOption(e.name, e.id));
	const defaultOption = dataAccount.map((e) => createOption(e.name, e.id));
	const defaultOption1 = dataMonth.map((e) => createOption(e.name, e.id));
	console.log('defaultOption', defaultOption1);
	const [hisob, setHisob] = useState({
		options2: [...defaultOption2],
		value2: undefined
	});
	const { value2 } = hisob;
	const [month, setMonth] = useState({
		options1: [...defaultOption1],
		value1: undefined
	});
	const { value1 } = month;

	const [hodim, setHodim] = useState({
		options: [...defaultOption],
		value: undefined
	});
	const { value } = hodim;
	const postHisobot = async (e) => {
		e.preventDefault();
		const new_contact = {
			date: handDate,
			income: 0,
			expense: chiqim,
			accountnumber: hisob.value2?.value,
			counterparty: 1,
			origin: 18,
			comment: izoh,
			employes: hodim.value?.value,
			monthone: month.value1?.value
			// monthtwo: oy.value4?.value
		};
		console.log('dateeee', handDate);
		const { dm } = await axios.post(`http://blmoliya.uz/api/finance/table/`, new_contact);
		console.log('new_contact', new_contact);
		setIzoh('');
		setMonth({
			value1: ''
		});
		setChiqim('');
		setHandDate('');

		setHodim({
			value: ''
		});
		setHisob({
			value2: ''
		});
	};

	const toggle = () => setModal(!modal);

	return (
		<div>
			<Button color="danger" onClick={toggle} style={{ width: '100%' }}>
				  
			</Button>
			<Modal isOpen={modal} toggle={toggle} className={className}>
				<ModalHeader toggle={toggle}>
					<div className="text-center text-muted mb-4">
						<small
							style={{
								fontSize: '16px',
								color: 'gray',
								fontWeight: '500',
								textAlign: 'center',
								marginTop: '10px'
							}}
						>
							  
						</small>
					</div>
				</ModalHeader>

				<ModalBody>
					<Row>
						<Col lg="12" md="12" style={{ marginBottom: 10 }}>
							<Card className="shadow border-0" style={{ backgroundColor: '#E5F0FF' }}>
								<CardBody className="px-lg-5 py-lg-3">
									<div className="text-center text-muted mb-4">
										<small
											style={{
												fontSize: '16px',
												color: 'gray',
												fontWeight: '500',
												textAlign: 'center',
												marginTop: '10px'
											}}
										></small>
									</div>
									<Form role="form">
										<FormGroup className="text-right">
											<div style={{ width: 200 }}>
												<Input
													type="date"
													value={handDate}
													onChange={(e) => setHandDate(e.target.value)}
												/>
											</div>
										</FormGroup>
										<FormGroup>
											<Select
												placeholder=""
												type="text"
												isClearable
												required={true}
												options={defaultOption2}
												value={value}
												onChange={(e) => setHodim({ value: e })}
											/>
										</FormGroup>
										<FormGroup>
											<InputGroup className="input-group-alternative mb-3">
												<Input
													placeholder=""
													type="number"
													value={chiqim}
													onChange={(e) => setChiqim(e.target.value)}
												/>
											</InputGroup>
										</FormGroup>

										<FormGroup>
											<Select
												placeholder=" "
												required
												isClearable
												onChange={(e) => setMonth({ value1: e })}
												options={defaultOption1}
												value={value1}
											/>
										</FormGroup>

										<FormGroup>
											<Select
												placeholder=""
												required
												isClearable
												onChange={(e) => setHisob({ value2: e })}
												options={defaultOption}
												value={value2}
											/>
										</FormGroup>

										<FormGroup>
											<textarea
												placeholder=""
												required={true}
												className="form-control"
												type="text"
												value={izoh}
												onChange={(e) => setIzoh(e.target.value)}
											/>
										</FormGroup>

										<div className="text-right">
											<Button
												className="mt-4"
												color="primary"
												type="button"
												onClick={postHisobot}
												disabled={!izoh}
											>
												
											</Button>
										</div>
									</Form>
								</CardBody>
							</Card>
						</Col>
					</Row>
				</ModalBody>
				{/* <ModalFooter>
					<FcPlus style={{ fontSize: 50, cursor: 'pointer' }} />
				</ModalFooter> */}
			</Modal>
		</div>
	);
};

export default ModalExample;
