import React, {  } from "react";
import { useDispatch,useSelector } from "react-redux";
import {deleteContact} from '../../../../redux/action/kassa/tableAction'
import {Button} from 'reactstrap'
import {MdDelete} from 'react-icons/md'
const Row = ({ row }) => {
 const  dispatch = useDispatch()
 const user = useSelector((state) => state.userLogin);
	const { userInfo } = user;
 
    
     const {accountnumber,date,accountnumbermoney,cashmoney,comment,counterparty,dollar,employes,expense,income,monthone,origin,plasticmoney,id} = row

  return (
    <tr>
      <td>{id}</td>
      {/* <td>{new Date().toLocaleDateString()}</td> */}
      <td>{date}</td>
      <td>{income}</td>
      <td>{expense}</td>
      <td>{accountnumber?.name}</td>
      <td>{counterparty?.name}</td>
      <td>{origin?.name}</td>
      <td>{employes?.name}</td>
      <td>{monthone?.name}</td>
      <td>{comment}</td>
      <td>{cashmoney}</td>
      <td>{plasticmoney}</td>
      <td>{accountnumbermoney}</td>
      <td>{dollar}</td>
      {userInfo?.isAdmin?  <td
          className="material-icons  text-danger"
          onClick={() => dispatch(deleteContact(id))}
        >
         <Button color="danger" onClick={()=>dispatch(deleteContact(id))}><MdDelete style={{fontSize:'20px'}}/></Button>
        </td>:""}
    
    </tr>
  );
};

export default Row;
