import {
     GET_PRODUCT_NAME,CREATE_PRODUCT_NAME,UPDATE_PRODUCT_NAME, DELETE_PRODUCT_NAME
        
      } from '../../typeXarid'
      const initialState = {
        product:[]
      }
      
      export const productReducer = (state=initialState, action) => {
          switch (action.type) {
              case GET_PRODUCT_NAME:
                return{
                  ...state,
                  product:action.payload
                }
    
              case CREATE_PRODUCT_NAME:
                const arr = []
                arr.push(action.payload)
                  return { 
                      ...state,
                     product:[...state.kont,...arr]
                        }
              case UPDATE_PRODUCT_NAME:
                          return {
                            ...state,
                            product: state.product.map((contact) =>
                              contact.id == action.id ? action.payload : contact
                            ),
                          };
              case DELETE_PRODUCT_NAME:
                const id = action.id    
                 return {
              ...state,
              product: state.product.filter((card) => card.id !== id)
                }
                
      
              default:
                  return state
          }
      }
      

      
      