import {
      GET_FURA,
      } from '../../typeXarid'
      const initialState = {
        fura:[]
      }
      
    
export const furaReducer = (state=initialState, action) => {
          switch (action.type) {
              case GET_FURA:
                return{
                  ...state,
                  fura:action.payload
                }
         default:
            return state
          }}
