import { createStore, combineReducers, applyMiddleware } from 'redux'
import thunk from 'redux-thunk'
import { composeWithDevTools } from 'redux-devtools-extension'
import {kassaReducer} from './reducer/kassaReduser'
import {userLoginReducer} from './reducer/userReducer'
import {kontReducer} from './reducer/kassa/kontReducer'
import {manbaReducer} from './reducer/kassa/manbaReducer'
import {employeeReducer} from './reducer/kassa/employeReducer'
import {acReducer} from './reducer/kassa/acReducer'
import {tableReducer} from './reducer/xarid/tableReucer'
import { furaReducer } from './reducer/xarid/furaReducer'
import { productReducer } from './reducer/xarid/productReducer'
import {contactReducer} from './reducer/kassa/tableReducer'
const reducer = combineReducers({
      kassa:kassaReducer,
      userLogin:userLoginReducer,
      kont:kontReducer,
      manba:manbaReducer,
      employee:employeeReducer,
      account:acReducer,
      xaridTable:tableReducer,
      fura:furaReducer,
      kassaTable:contactReducer,
      product:productReducer
})




const userInfoFromStorage = localStorage.getItem('userInfo') ?
    JSON.parse(localStorage.getItem('userInfo')) : null





const initialState = {
    userLogin: { userInfo: userInfoFromStorage },
}

const middleware = [thunk]

const store = createStore(reducer, initialState,
    composeWithDevTools(applyMiddleware(...middleware)))

export default store